import React from 'react'
import { Table } from 'react-bootstrap'
export const TicketTable = ({tickets }) => {
    return (
        <Table striped bordered hover>
            <thead>
                <tr>
                    <th>Ticket id</th>
                    <th>Event title</th>
                    <th>Description</th>
                    <th>Image</th>
                    <th>Price</th>
                    <th>Status</th>
                </tr>
            </thead>
            <tbody>
                {tickets.length ?
                    tickets.map(row =>
                    <tr key={row.id}>
                        <td>{row.id}</td>
                        <td>{row.Eventtitle}</td>
                            <td>{row.Description}</td>
                            <td><img src={row.img} width="50px" alt="ticket" /></td>
                        <td>Ksh. {row.Price}</td>
                        <td>{row.Status}</td>
                </tr>
                    ) :
                    <tr>
                        <td colSpan="4" className="text-center">No tickets intable</td>
                    </tr>
                    }
            </tbody>
            
        </Table>
        )
}