import React from 'react'
import './AddTicketForm.style.css'
import { Form, Button, Row, Col } from 'react-bootstrap'
import PropTypes from 'prop-types'

export const AddTicketForm = ({ handleOnSubmit, handleOnChange, Formdata }) => {
    return (
        <div className="jumbotron">
            <Form onSubmit={handleOnSubmit}>
                <Form.Group as={Row}>
                    <Form.Label col sm={3}>Event title</Form.Label>
                    <Col sm={9 }>
                    <Form.Control
                        type="text"
                        name="title"
                        value={Formdata.title}
                        onChange={handleOnChange}
                        placeholder="Enter name of event"
                        aria-placeholder="event"
                        required
                    /></Col>
                </Form.Group>
                <Form.Group>
                    <Form.Label>Description</Form.Label>
                    <Form.Control
                        as="textarea"
                        name="description"
                        value={Formdata.description}
                        onChange={handleOnChange}
                        rows="5"
                        placeholder="Describe the event"
                        aria-placeholder="password"
                        required
                    />

                </Form.Group>
                <Form.Group>
                    <Form.Label>Image</Form.Label>
                    <Form.Control
                        type="file"
                        name="image"
                        value={Formdata.image}
                        onChange={handleOnChange}
                        placeholder="Enter your poster"
                        aria-placeholder="poster"
                        required
                    />

                </Form.Group>
                <Form.Group>
                    <Form.Label>Price</Form.Label>
                    <Form.Control
                        type="text"
                        name="price"
                        value={Formdata.price}
                        onChange={handleOnChange}
                        placeholder="Enter price"
                        aria-placeholder="price"
                        required
                    />

                </Form.Group>
                <br />

                <Button type="submit" block variant="info">Add event</Button>
            </Form>
        </div>
        )
}

AddTicketForm.propTypes = {
    handleOnChange: PropTypes.func.isRequired,
    handleOnSubmit: PropTypes.func.isRequired,
    Formdata: PropTypes.object.isRequired,
}