import React from 'react'
import { Link } from 'react-router-dom'
import "./Events.comp.css"

export const Eventslist = ({ events }) => {
    return (

        <div className="Eventt">
                {events.length ?
                    events.map(row =>
                        <div className="eventList">
                            <div className="eventListItem" key={row.Id}>
                                
                                <Link to={`/event/${row.Id}` } >
                                    <img src={row.img} alt="event" className="eventListImg" />
                                </Link>
                                <h2>{row.Eventtitle}</h2>
                                <span>{row.Description}</span>
                                <h3>{row.Price}</h3>

                            </div>
                        </div>
                ) :
                <h2>No listed events</h2>
                }
                        </div>
           
    )
}
