import React from 'react'
import { Container, Row, Col, /*Button*/ } from 'react-bootstrap'
//import { TicketTable } from '../../components/ticketTable/ticketTable.comp'
import { BreadCrumbExample } from "../../components/breadCrumb/BreadCrumb.comp"
import events from '../../assets/data/DummyTicket.json'
import { Eventslist } from '../../components/events/Events.comp'

export const Events = () => {
    return (
        <Container>
            <Row>
                <Col>
                    <BreadCrumbExample page="Events" />
                </Col>
            </Row>
            <Row>
                <Col>
                    <Eventslist events={events} />
                </Col>
            </Row>
        </Container>
        )
}