import React, { useEffect } from 'react'
import {useState } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import { AddTicketForm } from '../../components/addTicket/AddTicketForm.comp'
import { BreadCrumbExample } from '../../components/breadCrumb/BreadCrumb.comp'

const initialFormdata = {
    title: '',
    description: '',
    image: '',
    price: '', 

}
export const Addticket = () => {

    const [Formdata, setFormdata] = useState(initialFormdata);
    useEffect(() => {}, [Formdata]);

    const handleOnChange = (e) => {
        const { name, value } = e.target;

        
        setFormdata({
            ...Formdata,
            [name]: value
        });
        
    }

    const handleOnSubmit = (e) => {
        e.preventDefault()

        console.log("Form submit received", Formdata)
    }
    return (
        <Container>
            <Row>
                <Col>
                    <BreadCrumbExample page="new ticket" />
                </Col>
            </Row>

            <Row>
                <Col>
                    <AddTicketForm
                        handleOnChange={handleOnChange}
                        handleOnSubmit={handleOnSubmit}
                        Formdata={Formdata}
                    />
                </Col>
            </Row>
        </Container>

        )
}