import React from 'react'
import './App.css';
import {
    BrowserRouter as Router,
    Routes,
    Route,
    
} from "react-router-dom";
//import { DefaultLayout } from './layout/DefaultLayout';
import { Addticket } from './pages/addTicket/Addticket.page';
import { Dashboard } from './pages/dashboard/Dashboard.page';
import { Events } from './pages/events/Event.page';
import { Event } from './pages/event/Event.page';
import { Entry } from './pages/entry/Entry.page';
import { QrCode } from './pages/qrCode/QrCode';
import { Header } from './layout/partials/Header'
import { Footer } from './layout/partials/Footer'
import { Fragment } from 'react';
import { PrivateRoute } from './components/privateRoute/PrivateRoute.comp';
import { Nopage } from './pages/noPage/Nopage.page';

function App() {
  return (
      <div className="App">
          <Router>
              
             
              <Routes>
                  <Route exact path="/" element={<Entry />} />


                  
                      <Route path="/dashboard/*" element={<Events />} />
                  
                  
                      <Route path="/admin-dashboard/*" element={<Dashboard />} />
                 
                  
                      <Route path="/admin-add-ticket/*" element={<Addticket />} />
                  
                  
                  <Route path="/event/:Eid" element={<Event />} />
                  <Route path="/ticketqr/:Qrid" element={<ticketQrCode /> } />
                  <Route path="*" element={<Nopage />} />
                  
              
              
              
                  

                  
              </Routes>
              
          </Router>
    </div>
  );
}

export default App;
