import React from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'
import { TicketTable } from '../../components/ticketTable/ticketTable.comp'
import { BreadCrumbExample } from "../../components/breadCrumb/BreadCrumb.comp"
import tickets from '../../assets/data/DummyTicket.json'
import { Link } from 'react-router-dom'
export const Dashboard = () => {
    return (
        <Container>
            <Row>
                <Col>
                    <BreadCrumbExample page="Dashboard" />
                </Col>
            </Row>
            <Row>
                <Col className="text-center mt-5 mb-2">
                    <Link to="/admin-add-ticket/*">
                        <Button variant="info" style={{ fontSize: "2rem", padding: "10px 30px" }}>Add new Event</Button>
                    </Link>
                </Col>
            </Row>
            <Row>
                <Col className="text-center mb-2">
                    <div>Total tickets: 3</div>
                    <div>pending: 0</div>
                </Col>
            </Row>
            <Row>
                <Col className="mt-2">
                    Recently added
                </Col>
            </Row>
            <br />
            <Row>
                <Col className="recent-ticket">
                    <TicketTable tickets={tickets} />
                </Col>
            </Row>
        </Container>
        )
}